.primary-button {
  display: flex;
  justify-content: center;
  align-items: center;
  color: $white;
  font-size: 18px;
  font-weight: 700;
  line-height: 4.33;
  background-color: $green;
  border: none;
  border-radius: 20px 0px;
  outline: none;
  cursor: pointer;
}

.secondary-button {
  display: flex;
  justify-content: center;
  align-items: center;
  color: $white;
  font-size: 16px;
  font-weight: 600;
  line-height: normal;
  background-color: $brown;
  border: none;
  border-radius: 15px 0px;
  outline: none;
  cursor: pointer;
}
